.Card {
  width: 300px;
  height: 170px;
  perspective: 1000px;
  margin-bottom: 20px;
  &__cardInner {
    transition: transform 1s;
    transform-style: preserve-3d;
    width: 100%;
    height: 100%;
    position: relative;
    &--isFlipped {
      transform: rotateY(180deg);
    }
  }
  &__cardFace {
    position: absolute;
    width: 100%;
    height: 100%;
    color: $main-color;
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
    overflow: hidden;
    border-radius: 25px;
    box-shadow: 0px 3px 18px 3px rgba(0, 0, 0, 0.2);
    &--front {
      background-image: linear-gradient(to bottom right, $card-color, rgb(255, 255, 255));
     //background-color: $card-color;
      text-align: center;
      display: flex;
      align-items: center;
      justify-content: center;
    }
    &--back {
      background-image: linear-gradient(to bottom right, $card-color, #ffffff);
      //background-color: $card-color;
      text-align: center;
      display: flex;
      align-items: center;
      justify-content: center;
      transform: rotateY(180deg);
    }
    &__cardContent {
      width: 100%;
      height: 100%;
    }
    &__body {
      padding: 30px;
    }
    &__cardHeader {
      position: relative;
      padding: 30px 30px 40px;
      &h2 {
        font-size: 32px;
        font-weight: 900;
        text-transform: uppercase;
        text-align: center;
      }
    }
  }
}
