.AddWords {
    color: white;
    min-height: 100vh;
    font-size: 20px;
    display: flex;
    flex-direction: column;
    align-items: center;
    &__form {
        display: flex;
        flex-direction: column;
        align-items: center;
        background-color: $second-color-white;
        width: 90%;
        margin-top: 100px;
        padding: 10px;
        border-radius: 10px;
        box-shadow: 0 2px 10px rgba(128, 128, 128, 0.158);
    }
    &__formHeader {
        margin-bottom: 30px;
        font-weight: 300;
    }
    &__engWordInput,
    &__plWordInput,
    &__wordCategoryInput {
        border-radius: 10px;
        height: 45px;
        margin: 0 0 20px 20px;
        background-color: transparent;
        border: 1px solid rgba(255, 255, 255, 0.425);
        color: rgba(255, 255, 255, 0.425);
        outline: none;
        padding-left: 10px;
    }
    &__formButton {
        padding: 10px 60px;
        border-radius: 30px;
        margin: 20px 0;
        outline: none;
        font-weight: 500;
        letter-spacing: 1px;
        border: none;
    }
}