.Home {
  color: white;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  &__headerWrapper {
    flex-basis: 230px;
    display: flex;
    justify-content: center;
  }
  &__contentWrapper,
  &__contentWrapperFlashlights {
    background-color: $second-color-white;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
    min-height: calc(100vh - 230px);
    border-top-left-radius: 60px;
    border-top-right-radius: 60px;
    position: relative;
  }
  &__contentHeader {
    color: $main-color;
    font-size: 25px;
  }
  &__contentBack {
    color: $main-color;
    font-size: 21px;
    font-weight: 600;
  }
  &__flashlightsCategoriesWrapper {
    width: 100%;
    height: 100%;
    padding: 10px 40px 10px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    align-items: center;
  }
  &__flashlightsCategories {
    width: 125px;
    height: 125px;
    border: 2px solid $main-color;
    background-color: $third-color;
    border-radius: 20px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    &:nth-child(2) {
        margin-bottom: 20px;
    }
    &:nth-child(1) {
        margin-bottom: 20px;
    }
  }
  &__flashlightsCategoriesBody {
    font-size: 25px;
    font-weight: 600;
    color: $main-color;
    margin-bottom: 10px;
    &--txt {
        font-size: 20px;
        color: $main-color;
    }
  }
  &__AddWordsButton,
  &__RandomTenButton {
    width: 70%;
    padding: 16px 0;
    border: none;
    background-color: $main-color;
    font-size: 18px;
    border-radius: 50px;
    outline: none;
    color: white;
    text-align: center;
    letter-spacing: 1px;
    font-weight: 500;
    box-shadow: 0 2px 10px 5px rgba(0, 0, 0, 0.137);
  }
  &__headerImage {
    align-self: flex-end;
    width: 260px;
    transform: translateY(1px);
  }
}
